<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
   

<div class="header">
  <ul class="header-button-left">
    <li  v-on:click="step = 1">Cancel</li>
  </ul>

  <ul class="header-button-right">
    <li v-if="step == 2" v-on:click="step = step + 1">Next</li> 
  </ul>

    <ul class="header-button-right">
      <li v-if=" step == 3" v-on:click="update">DONE</li>
  </ul>


  <img src="./assets/logo.png" class="logo">
</div>

{{ name }} <!--이렇게 쓰면 return 값이 출력된다. -->
<!-- {{$store.getters.NAME}} -->
<!-- {{$store.getters.LIKES}} -->

<!-- {{$store.commit('SET_NAME','박') }} -->
<button v-on:click="update_btn">click</button>

<Body v-on:hyodo="작성글 = $event" v-bind:포스팅들="포스팅들" :step="step" :이미지경로="이미지경로" :선택한필터="선택한필터"/>
<button v-on:click="showMore">더보기</button>
<div class="footer">
  <ul class="footer-button-plus">
    <input type="file" id="file" class="inputfile" v-on:change="upload">
    <label for="file" class="input-plus">+</label>

  </ul>
</div>

  </div>

</template>

<script>
import Postdata from './assets/postdata.js';
import Body from './components/Body.vue';
import EventBus from './EventBus.js';
import axios from 'axios';


export default {
  name: 'app',
  data () {
    return {  //저장공간
      포스팅들 : Postdata,
      step : 1,
      이미지경로 : '',
      작성글 : '',
      선택한필터:'',
    }
  },
 
  methods : {
    update() {
      //포스팅들이라는 [데이터]에 내 데이터 추가 [여기,1,2,3], .unshift(1) 사용하면 1이 처음에 추가된다. push() 맨뒤에 추가.
      //데이터 형식에 맞게 {} 오브젝트임.
       let 내데이터 = {
        name: "Kimhyukmin",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.이미지경로,
        likes: 36,
        date: 'May 15',
        liked: false,
        caption: this.작성글,
        filter: this.선택한필터,
      
      }
      this.포스팅들.unshift(내데이터);
      this.step = 1;
    },
    upload(e) {
      //1. 업로드 한 이미지경로알아내기, 2. step을 2로 변경
      let file = e.target.files;
      let reader = new FileReader();
      reader.readAsDataURL(file[0]);
      reader.onload = e => {
      this.이미지경로 = e.target.result;
      
      }

      this.step = 2;

    },
    showMore() {
      
      axios.get('/more.json').then(결과 => {
        //데이터를 this.포스팅들에 추가함 axios사용
      this.포스팅들.push(결과.data);
      });

    },
    update_btn() {
      this.$store.dispatch('GET_NAME','박');
    }
  },
  computed:{  //데이터 저장공간
    name() {
      return this.$store.getters.NAME
    }
  },
   components: {
    Body,
    EventBus,
    
  },
  mounted() {
    EventBus.$on('필터선택함',(받아온자료) => {
      this.선택한필터 = 받아온자료;
    })
  },
}
</script>

