import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export let store = new Vuex.Store({
  state: {
      //데이터 저장공간, ajax요청으로 넣는다.
      name : 'john',
      likes : 30,
  },
  getters : {
      // 데이터 가져오는 함수, john을 가져오는 함수를 만든다.
    NAME(state) {    //데이터 이름과 동일하게 대문자로쓴다.
        return state.name
    },
    LIKES(state){
        return state.likes  //.filter()
    }
  },
  mutations: {
      //데이터 수정하는 함수를 만든다.
      SET_NAME(state,이름) {
        state.name = 이름
      }
  },
  actions : {
    //mutations를 동작시키는 함수. 비동기 통신 가능. ajax 요청하고 싶을떄.
   
        GET_NAME (context, payload) {
          axios.get('/more.json')
            .then( 가져온데이터 => {
               context.commit( "SET_NAME" , 가져온데이터);
            });
        }
      
      

  }  
})
