<template>
    <div 
    v-on:click="sendFilter" 
    v-bind:class="['filter-item',필터]"  
    :style="{ backgroundImage :  `url( ${이미지경로} )`}"> 
    
    <slot></slot>
    </div>
</template>

<script>
import EventBus from './../EventBus';

export default {
    props : {
        이미지경로 : String,
        필터 : String,
    },
    methods : {
        sendFilter() {
            EventBus.$emit('필터선택함',this.필터)
        }
    }
}
</script>

<style>
.filter-item {
width: 100px;
height: 100px;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}

</style>
